import React, { useState, useEffect } from 'react'
import { Timeline, Divider } from 'antd'
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom'
import moment from 'moment'
import { Select } from 'antd'
import { Layout } from 'antd'

const { Content } = Layout

const { Option } = Select

export default function TimelineComp(props) {
	
	let isNotEmpty =
		props.data !== null &&
			props.data !== undefined &&
			Object.entries(props.data).length > 0

	const [dataLineCompleted, setdataLineCompleted] = useState([])
	const [useFullData, setuseFullData] = useState([])

	const handleChangeClick = (e) => {
		//Get the string back from the div, date value from string
		let date = e.currentTarget.textContent.slice(0, 10).trim()
		// console.log(date)
		props.setState((prev) => ({
			...prev,
			value: moment(date),
			selectedValue: moment(date),
		}))
	}

	useEffect(() => {
		//On page refresh, reset the data
		let currentData = props.getCurrentData(
			props.data,
			props.setdata,
			props.setState,
			props.state.value,
			props.state.selectedValue
		)

		//Setting the links
		document.getElementById('savingMode').className =
			'ant-menu-item ant-menu-item-only-child'
		document.getElementById('calendo').className =
			'ant-menu-item ant-menu-item-only-child'
		document.getElementById('addTask').className =
			'ant-menu-item ant-menu-item-only-child'
		document.getElementById('listTask').className =
			'ant-menu-item ant-menu-item-only-child'
		document.getElementById('timeline').className =
			'ant-menu-item ant-menu-item-only-child ant-menu-item-selected'

		if (
			currentData !== null &&
			currentData !== undefined &&
			props.data !== null &&
			props.data !== undefined
		) {
			let dataLine = null
			let dataLineCompletedCopy = []
			let valueInput = null
			let priority = null
			let hour = ''
			let currentDate = new Date()

			let fullData = []
			let data = []
			let color = ''
			// console.log(Object.entries(currentData).length)

			//Get all the values back and store them in an array
			if (Object.entries(currentData).length > 0) {
				//Pour chaque année
				Object.keys(currentData).map((year) => {
					// console.log(props.data[year])

					//Pour chaque mois
					Object.keys(currentData[year]).map((month) => {
						// console.log(props.data[year][month])

						//Pour chaque jour (récupérer la date et la passer)
						Object.keys(currentData[year][month]).map((day) => {
							// console.log(props.data[year][month][day])
							// A passer props.data[year][month][day].fullDate

							// console.log(currentData[year][month][day].fullDate)

							currentDate = currentData[year][month][day].fullDate

							//In case of page reload,reset current data a moment object
							if (typeof currentDate === 'string') {
								currentDate = moment(currentDate)
							}
							//Acceder au content et pourchaque valueInput du content
							Object.keys(
								currentData[year][month][day].content
							).map((index) => {
								hour = index + 'h00'

								valueInput =
									currentData[year][month][day].content[index]
										.valueInput
								priority =
									currentData[year][month][day].content[index]
										.priority

								//Setting the color
								switch (priority) {
									case 'normal':
										color = 'green'
										break
									case 'important':
										color = '#fbc531'
										break
									case 'urgent':
										color = 'red'
										break
									case 'completed':
										color = 'blue'
										break

									default:
										break
								}

								data = []
								// fullData = []
								data.push(index)
								data.push(currentDate.format('YYYY-MM-DD'))
								data.push(hour)
								data.push(valueInput)
								data.push(priority)
								data.push(color)

								fullData.push(data)
							})
						})
					})
				})

				fullData = fullData.sort(
					(a, b) => new Date(b[1]) - new Date(a[1])
				)
				//0=index 1=dat 2=hour 3=valueInput 4= priority 5=color
				for (let i = 0; i < fullData.length; i++) {
					//Je construit la data
					dataLine = (
						<Timeline.Item
							color={fullData[i][5]}
							key={fullData[i][0] + fullData[i][1]}
						>
							<Link to='/AddTasks'>
								<div
									onClick={(e) => handleChangeClick(e)}
									className='timeline-line'
								>
									<span className='timeline-date'>
										{fullData[i][1]}
									</span>
									<span className='timeline-hour'>
										{fullData[i][2]}
									</span>
									<span className='timeline-input'>
										{' '}
										{fullData[i][3]}{' '}
									</span>
									<span className='timeline-priority'>
										{' '}
										{fullData[i][4]}{' '}
									</span>
								</div>
							</Link>
						</Timeline.Item>
					)

					dataLineCompletedCopy.push(dataLine)
				}

				//Copie la timeline
				setdataLineCompleted(dataLineCompletedCopy)
				//Copie le tableau de data
				setuseFullData(fullData)
			}
		}
	}, [])

	function handleSortDate(value) {
		if (useFullData.length > 0) {
			//Cuurent array being filtered
			let fullData = useFullData.slice()

			//Printing values to Timeline Item
			let dataPrint = []

			if (value === 'ASC') {
				fullData = fullData.sort(
					(a, b) => new Date(b[1]) - new Date(a[1])
				)
			} else if (value === 'DESC') {
				fullData = fullData.sort(
					(a, b) => new Date(a[1]) - new Date(b[1])
				)
			}
			for (let i = 0; i < fullData.length; i++) {
				//Je construit la data
				//Reconstruire dataPrintState
				dataPrint.push(
					<Timeline.Item
						color={fullData[i][5]}
						key={fullData[i][0] + fullData[i][1]}
					>
						<Link to='/AddTasks'>
							<div
								onClick={(e) => handleChangeClick(e)}
								className='timeline-line'
							>
								<span className='timeline-date'>
									{fullData[i][1]}
								</span>
								<span className='timeline-hour'>
									{fullData[i][2]}
								</span>
								<span className='timeline-input'>
									{' '}
									{fullData[i][3]}{' '}
								</span>
								<span className='timeline-priority'>
									{' '}
									{fullData[i][4]}{' '}
								</span>
							</div>
						</Link>
					</Timeline.Item>
				)
			}

			setdataLineCompleted(dataPrint)

			fullData = []
			dataPrint = []
		}
	}

	return (
		<Content style={{ padding: '0 50px', height: '100vh' }}>
			<div className='timeline-main'>
				<h1 className='timeline-main-title'>Timeline</h1>
				<div>
					<Select
						className='timeline-select'
						placeholder='Oder By Date'
						style={{ width: 166 }}
						onChange={handleSortDate}
					>
						<Option value='ASC'>Ascending Dates</Option>
						<Option value='DESC'>Descending Dates</Option>
					</Select>

					<Divider>
						{!isNotEmpty ? (
							<div>
								<i className='fas fa-info-circle'></i> Hello,
								Sorry No Data Yet
							</div>
						) : (
							<div>
								<i className='far fa-clock'></i> Your Route:
							</div>
						)}
					</Divider>

					<Timeline>{dataLineCompleted.map((item) => item)}</Timeline>
				</div>
			</div>
		</Content>
	)
}
