import React, { useState } from 'react'
import { Calendar, Alert } from 'antd'

// import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom'

import { Layout } from 'antd'

const { Content } = Layout

export default function CalendarComp({
	state,
	setState,
	setupdate,
}) {
	const [privateMode, setprivateMode] = useState('month')

	const onPanelChange = (val, mode) => {
		// console.log(val.format('YYYY-MM-DD'), mode)
		setState((prev) => ({
			...prev,
			value: val,
		}))
		// console.log(mode)
		setprivateMode(mode)
	}

	

	const onSelect = (val) => {
		setState({
			value: val,
			selectedValue: val,
		})


		// console.log('helloffrom')
		// console.log('ArrayValue')
		// console.log(arrayValue)
		// console.log('InputArray')
		// console.log(inputArray)
		
		setupdate(val.format('YYYY'), val.format('MM'), val.format('DD'))
		//check if data for that day to be sent to addEditTask component as array
		// console.log(update)
	
	}

	// const onChange = () => {
	// 	console.log('changed')
	// }

	

	return (
		<Content style={{ padding: '0 50px' }}>
			<h1 className='main-calendar2-title'>Add some tasks</h1>

			<Alert
				message={
					privateMode === 'year'
						? `You selected date: ${
								state.selectedValue &&
								state.selectedValue.format('YYYY-MM')
						  }`
						: `You selected date: ${
								state.selectedValue &&
								state.selectedValue.format('YYYY-MM-DD')
						  }`
				}
			/>

			<Calendar
				fullscreen={false}
				value={state.value}
				onSelect={onSelect}
				// onChange={onChange}
				onPanelChange={onPanelChange}
			/>
		</Content>
	)
}
