import React, { useState, useEffect } from 'react'
import { Calendar, Alert, Badge } from 'antd'
// import Alert from './Alert'
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom'

import { Layout } from 'antd'

const { Content } = Layout

export default function CalendarComp({
	state,
	setState,
	data,
}) {
	const [privateMode, setprivateMode] = useState('month')

	useEffect(() => {
		document.getElementById('savingMode').className =
			'ant-menu-item ant-menu-item-only-child'
		document.getElementById('calendo').className =
			'ant-menu-item ant-menu-item-only-child ant-menu-item-selected'
		document.getElementById('addTask').className =
			'ant-menu-item ant-menu-item-only-child'
		document.getElementById('listTask').className =
			'ant-menu-item ant-menu-item-only-child'
		document.getElementById('timeline').className =
			'ant-menu-item ant-menu-item-only-child'
		//On page refresh, reset the data
		// let currentData = getCurrentData(data, setdata, setState, state.value, state.selectedValue)
	})

	//Value pour le calendrier uniquement
	const onPanelChange = (val, mode) => {
		// console.log('Panel change')

		// console.log(val.format('YYYY-MM-DD'), mode)
		setState((prev) => ({
			...prev,
			value: val,
		}))
		// console.log(mode)
		setprivateMode(mode)
	}

	const onSelect = (val) => {
		setState((prev) => ({
			...prev,
			value: val,
			selectedValue: val,
		}))

		//check if data for that day to be sent to addEditTask component as array
		// console.log('helloffrom')
		// console.log(val.format('YYYY-MM-DD'))

		//Deplacement dans componentDidMount AddEditTask
		// if (Object.entries(data).length > 0){
		// 	// console.log('1')
		// 	if (data[val.format('YYYY')]){
		// 		// console.log('2')
		// 		if (data[val.format('YYYY')][val.format('MM')]){
		// 			// console.log('3')
		// 			if (data[val.format('YYYY')][val.format('MM')][val.format('DD')]){
		// 				// console.log('4')
		// 				let tempArrayValue = new Array(24).fill(null)
		// 				let tempArrayPriority = new Array(24).fill(null)
		// 				//Because there can be no entries for that hour
		// 				if(Object.entries(data[val.format('YYYY')][val.format('MM')][val.format('DD')].content).length > 0){
		// 					Object.keys(data[val.format('YYYY')][val.format('MM')][val.format('DD')].content).map((key) => {
		// 						// console.log(this.props.data[this.state.year][this.state.month][this.state.day].content[key])
		// 						tempArrayValue[key] = data[val.format('YYYY')][val.format('MM')][val.format('DD')].content[key].valueInput
		// 						tempArrayPriority[key] = data[val.format('YYYY')][val.format('MM')][val.format('DD')].content[key].priority
		// 					})

		// 					// console.log(tempArrayValue)
		// 					// console.log(tempArrayPriority)
		// 					setinputArray(tempArrayValue)
		// 					setpriorityArray(tempArrayPriority)
		// 				}
		// 			}
		// 		}
		// 	}
		// }
	}

	//Day data
	const dateCellRender = (val) => {
		if(data !== null && data !== undefined){
			let date = val.format('YYYY-MM-DD')
			let dataExist =
				data[val.format('YYYY')] &&
				data[val.format('YYYY')][val.format('MM')] &&
				data[val.format('YYYY')][val.format('MM')][val.format('DD')] &&
				data[val.format('YYYY')][val.format('MM')][val.format('DD')]
					.content

			// console.log(dataExist)

			const listData = dataExist
				? data[val.format('YYYY')][val.format('MM')][val.format('DD')]
						.content
				: ''
			// console.log(val.format('DD'))

			// console.log(listData)
			// console.log(val.format('YYYY-MM-DD'))

			//Defining priorities
			let normal = 0
			let important = 0
			let urgent = 0
			let priorityArray = []
			let displayPriority = []
			let priorityDefaultNames = ['Normal', 'Important', 'Urgent']
			let priorityNames = ['success', 'warning', 'error']

			if (listData && Object.entries(listData).length > 0) {
				Object.keys(listData).map((key) =>
					// console.log(listData[key])
					priorityArray.push(listData[key].priority)
				)

				for (let index = 0; index < priorityArray.length; index++) {
					if (priorityArray[index] === 'normal') {
						normal++
					}

					if (priorityArray[index] === 'important') {
						important++
					}

					if (priorityArray[index] === 'urgent') {
						urgent++
					}
				}

				displayPriority.push(normal)
				displayPriority.push(important)
				displayPriority.push(urgent)
			}

			return (
				<ul className='events'>
					{val.format('MM-DD') === state.value.format('MM-DD') && (
						<li key={val.date()} className='main-calendar-link'>
							<Link to='/AddTasks'>Edit</Link>
						</li>
					)}
					{displayPriority.map(
						(priority, index) =>
							val.format('MM-DD') !==
								state.value.format('MM-DD') &&
							(priority !== 0 ? (
								<li
									key={date + ' ' + priority + index}
									className='main-calendar-link2'
								>
									<Link to='/AddTasks'>
										<Badge
											// status={item.type}
											status={priorityNames[index]}
											text={
												priority +
												' ' +
												priorityDefaultNames[index]
											}
										/>
									</Link>
								</li>
							) : null)
					)}
				</ul>
			)
		}
	}

	const monthCellRender = (val) => {
		if(data !== null && data !== undefined){
			let date = val.format('YYYY-MM')
			let dataExist =
				data[val.format('YYYY')] &&
				data[val.format('YYYY')][val.format('MM')] &&
				Object.entries(data[val.format('YYYY')][val.format('MM')])
					.length > 0

			// console.log(dataExist)

			const listData = dataExist
				? data[val.format('YYYY')][val.format('MM')]
				: ''
			// console.log(val.format('DD'))

			// console.log(listData)
			// console.log(val.format('YYYY-MM-DD'))

			//Defining priorities
			let normal = 0
			let important = 0
			let urgent = 0
			let priorityArray = []
			let displayPriority = []
			let priorityDefaultNames = ['Normal', 'Important', 'Urgent']
			let priorityNames = ['success', 'warning', 'error']

			let contentList = []

			if (listData && Object.entries(listData).length > 0) {
				// console.log('month ok1')

				//Chaque content
				Object.keys(listData).map((key) =>
					//Mapper sur content
					contentList.push({ ...listData[key].content })
				)
				// console.log('contentList')
				// console.log(contentList)

				//Map sur chaque elment du content
				for (let index = 0; index < contentList.length; index++) {
					// console.log('contentList[index]')
					// console.log(contentList[index])
					//Map sur chaque element de l'element du content
					Object.keys(contentList[index]).map((key) => {
						// console.log('[key]')
						// console.log(contentList[index][key])
						priorityArray.push(contentList[index][key].priority)
					})
				}

				// console.log(priorityArray)

				for (let index = 0; index < priorityArray.length; index++) {
					if (priorityArray[index] === 'normal') {
						normal++
					}

					if (priorityArray[index] === 'important') {
						important++
					}

					if (priorityArray[index] === 'urgent') {
						urgent++
					}
				}

				displayPriority.push(normal)
				displayPriority.push(important)
				displayPriority.push(urgent)
			}

			return (
				<ul className='notes-month'>
					{displayPriority.map(
						(priority, index) =>
							val.format('MM-DD') !==
								state.value.format('MM-DD') &&
							(priority !== 0 ? (
								<li key={date + ' ' + priority + index}>
									<Badge
										// status={item.type}
										status={priorityNames[index]}
										text={
											priority +
											' ' +
											priorityDefaultNames[index]
										}
									/>
								</li>
							) : null)
					)}
				</ul>
			)
		}
	}

	// const onChange = () => {
	// 	console.log('changed')
	// }

	return (
		<Content style={{ padding: '0 50px' }}>
			<h1 className='main-calendar-title'>Calendo</h1>

			<Alert
				message={
					privateMode === 'year'
						? `You selected date: ${
								state.value && state.value.format('YYYY-MM')
						  }`
						: `You selected date: ${
								state.value && state.value.format('YYYY-MM-DD')
						  }`
				}
			/>

			<Calendar
				className='main-calendar'
				value={state.value}
				onSelect={onSelect}
				// onChange={onChange}
				onPanelChange={onPanelChange}
				dateCellRender={dateCellRender}
				monthCellRender={monthCellRender}
			/>
		</Content>
	)
}
