import React, { useState, useEffect, useRef } from 'react'
import { DatePicker, Radio } from 'antd'
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom'

import { Input } from 'antd'
import { List, Divider } from 'antd'

import { Select } from 'antd'
import moment from 'moment'
const { Search } = Input

const { Option } = Select


export default function ListAllTasks({ state, setState, data, setdata, getCurrentData }) {

	//Print la data dans la liste
	const [dataPrintState, setdataPrintState] = useState([])
	//Contient toutes les données
	const [dataArrayState, setdataArrayState] = useState([])
	//Contient toutes les priorites normales
	const [normal, setNormal] = useState([])
	//Contient toutes les priorites normales
	const [important, setImportant] = useState([])
	//Contient toutes les priorites importantes
	const [urgent, setUrgent] = useState([])
	//Contient toutes les priorites urgentes
	const [completed, setCompleted] = useState([])
	//choix de priorite
	const [priorityChoice, setPriorityChoice] = useState(0)
	//reset the date field to empty
	const [dateFieldValue, setdateFieldValue] = useState(null) //moment(Date.now).format('YYYY-MM-DD')
	//to keep the filter option ASC DESC in handleSearchMain and handleChangePriority
	const [filterAscDesc, setFilterAscDesc] = useState('')

	const inputEl = useRef(null)
	const inputSearch = useRef(null)
	//SetState
	// history: history.concat([{
	//     squares: squares,
	//   }])
	//DEclaration
	// history: [{
	//     squares: Array(9).fill(null),
	//   }]

	//array qui affiche avec JSX
	let dataPrint = []
	let currentDate = new Date()
	// let valueInput = null
	// let priority = null
	let hour = ''

	const handleSearchMain = (param) => {
		setdateFieldValue(null)
		let currentArray = []
		let test = false
		let data = []

		switch (priorityChoice) {
			case 0:
				// default all tasks
				currentArray = dataArrayState.slice()
				break
			case 1:
				// Normal tasks
				currentArray = normal.slice()
				break
			case 2:
				// Imprtant tasks
				currentArray = important.slice()
				break
			case 3:
				// Urgent tasks
				currentArray = urgent.slice()
				break
			case 4:
				// Completed tasks
				currentArray = completed.slice()
				break
			default:
		}
		// console.log('handleSEARCH')
		// console.log(filterAscDesc)

		if (filterAscDesc === 'ASC') {
			currentArray = currentArray.sort(
				(a, b) => new Date(b[1]) - new Date(a[1])
			)
		} else if (filterAscDesc === 'DESC') {
			currentArray = currentArray.sort(
				(a, b) => new Date(a[1]) - new Date(b[1])
			)
		}
		// console.log(dataCurrentArray)
		for (let i = 0; i < currentArray.length; i++) {
			// Tous les elements mais il faut valueInput dataCurrentArray[i];
			//Pour chaque lettre de valueInput dataCurrentArray[i][3]
			test = false
			//J to access each letters
			for (let j = 0; j < currentArray[i][3].length; j++) {
				if (
					currentArray[i][3]
						.toUpperCase()
						.indexOf(param.toUpperCase()) > -1
				) {
					test = true
					//push dataCurrentArray[i] dans l'array avec tous les champs
					//En sortir mettre current array a jour avec le new array ou tout a ete pushed
					// console.log(currentArray[i][3])
				}
			}

			if (test) {
				data.push(currentArray[i])
			}
		}

		//Reconstruire dataPrintState
		for (let i = 0; i < data.length; i++) {
			//Pour chaque data dans l'element courant

			dataPrint.push(
				<div key={data[i][0] + data[i][1]}>
					<Link to='/AddTasks'>
						<div
							onClick={(e) => handleChangeClick(e)}
							className='listtask-item'
						>
							<span> {data[i][1]} </span>
							<span> {data[i][2]} </span>
							<span> {data[i][3]} </span>
							<span> {data[i][4]} </span>
						</div>
					</Link>
				</div>
			)
		}

		setdataPrintState(dataPrint)
		data = []
	}

	const handleChangeSearch = (event) => {
		handleSearchMain(event.target.value)
	}

	const handleSearch = (searchString) => {
		handleSearchMain(searchString)
	}

	useEffect(() => {
		//On page refresh, reset the data
		let currentData = getCurrentData(data, setdata, setState, state.value, state.selectedValue)
		
		//Setting the links
		document.getElementById('savingMode').className = 'ant-menu-item ant-menu-item-only-child'
		document.getElementById('calendo').className = 'ant-menu-item ant-menu-item-only-child'
		document.getElementById('addTask').className = 'ant-menu-item ant-menu-item-only-child'
		document.getElementById('listTask').className = 'ant-menu-item ant-menu-item-only-child ant-menu-item-selected'
		document.getElementById('timeline').className = 'ant-menu-item ant-menu-item-only-child'
		if(currentData !== null && currentData !== undefined && data !== null && data !== undefined){
		//dataLine rempli data array
		let dataLine = []
		//array qui filtre
		let dataArray = []
		//Get all the values back and store them in an array
		if (Object.entries(currentData).length > 0) {
			//Pour chaque année
			Object.keys(currentData).map((year) => {
				// console.log(data[year])

				//Pour chaque mois
				Object.keys(currentData[year]).map((month) => {
					// console.log(data[year][month])

					//Pour chaque jour (récupérer la date et la passer)
					Object.keys(currentData[year][month]).map((day) => {
						// console.log(data[year][month][day])
						// A passer data[year][month][day].fullDate

						// console.log(currentData[year][month][day].fullDate)

						currentDate = currentData[year][month][day].fullDate
						//In case of page reload,reset current data a moment object
						if(typeof currentDate === 'string'){
							currentDate = moment(currentDate)
						}
						//Acceder au content et pourchaque valueInput du content
						Object.keys(currentData[year][month][day].content).map(
							(index) => {
								// console.log('On mappe')
								// console.log('le jour')

								// console.log(currentDate.format('YYYY-MM-DD'))

								// console.log('l index')
								// console.log(index)

								hour = index + 'h00'

								// console.log('hour')
								// console.log(hour)

								// console.log(
								// 	data[year][month][day].content[index]
								// 		.valueInput
								// )

								//array qui filtre
								dataLine = []
								dataLine.push(index)
								dataLine.push(currentDate.format('YYYY-MM-DD'))
								dataLine.push(hour)
								dataLine.push(
									currentData[year][month][day].content[index]
										.valueInput
								)
								dataLine.push(
									currentData[year][month][day].content[index]
										.priority
								)

								dataArray.push(dataLine)
								// console.log('dataArray')
								// console.log(dataArray)
							}
						)
					})
				})
			})

			let normal = []
			let important = []
			let urgent = []
			let completed = []

			for (let i = 0; i < dataArray.length; i++) {
				//Remplir un array avec juste normal
				if (dataArray[i][4] === 'normal') {
					normal.push(dataArray[i])
					//Remplir un array avec les importants
				} else if (dataArray[i][4] === 'important') {
					important.push(dataArray[i])
					//Remplir un array avec les urgents
				} else if (dataArray[i][4] === 'urgent') {
					urgent.push(dataArray[i])
					//Remplir un array avec les completed
				} else if (dataArray[i][4] === 'completed') {
					completed.push(dataArray[i])
				}
			}

			setNormal(normal)
			setImportant(important)
			setUrgent(urgent)
			setCompleted(completed)

			normal = []
			important = []
			urgent = []
			completed = []

			//array qui affiche
			//0=index  1=date  2=heure 3=valueInput 4=priority
			//on parcours dataArray
			for (let i = 0; i < dataArray.length; i++) {
				//Pour chaque data dans l'element courant

				dataPrint.push(
					<div key={dataArray[i][0] + dataArray[i][1]}>
						<Link to='/AddTasks'>
							<div
								onClick={(e) => handleChangeClick(e)}
								className='listtask-item'
							>
								<span> {dataArray[i][1]} </span>
								<span> {dataArray[i][2]} </span>
								<span> {dataArray[i][3]} </span>
								<span> {dataArray[i][4]} </span>
							</div>
						</Link>
					</div>
				)
			}
			// console.log('dataPrint debut')

			// console.log(dataPrint)
		}
		setdataArrayState(dataArray)
		setdataPrintState(dataPrint)
	}
	}, [])

	const handleChangeClick = (e) => {
		//Get the string back from the div, date value from string
		let date = e.currentTarget.textContent.slice(0, 12).trim()
		// console.log(date)
		//Update general date
		setState((prev) => ({
			...prev,
			value: moment(date),
			selectedValue: moment(date),
		}))
	}

	const handleChangePriority = (param) => {
		// inputEl.current.focus()
		// console.log(inputEl.current)
		// inputEl.current.props.value = ''
		setdateFieldValue(null)
		// inputEl.current.state.value = ''
		inputSearch.current.focus()
		//defini la priorité pour handleSearch function
		setPriorityChoice(param.target.value)
		// console.log(param.target.value)
		// setsearchParam(param.target.value)
		let dataPrint = []
		let dataArray = []
		let currentArray = []
		//0=index  1=date  2=heure 3=valueInput 4=priority
		switch (param.target.value) {
			case 0:
				// default all tasks
				currentArray = dataArrayState.slice()
				break
			case 1:
				// Normal tasks
				// console.log('cas 1')
				currentArray = normal.slice()
				break
			case 2:
				// Imprtant tasks
				// console.log('cas 2')
				currentArray = important.slice()
				break
			case 3:
				// Urgent tasks
				// console.log('cas 3')
				currentArray = urgent.slice()
				break
			case 4:
				// Completed tasks
				// console.log('cas 4')
				currentArray = completed.slice()
				break
				default:
		}

		if (filterAscDesc === 'ASC') {
			currentArray = currentArray.sort(
				(a, b) => new Date(b[1]) - new Date(a[1])
			)
		} else if (filterAscDesc === 'DESC') {
			currentArray = currentArray.sort(
				(a, b) => new Date(a[1]) - new Date(b[1])
			)
		}

		for (let i = 0; i < currentArray.length; i++) {
			//Pour chaque data dans l'element courant
			dataArray.push(currentArray[i])
			dataPrint.push(
				<div
					key={currentArray[i][0] + currentArray[i][1]}
				>
					<Link to='/AddTasks'>
						<div onClick={(e) => handleChangeClick(e)} className='listtask-item'>
							<span> {currentArray[i][1]} </span>
							<span> {currentArray[i][2]} </span>
							<span> {currentArray[i][3]} </span>
							<span> {currentArray[i][4]} </span>
						</div>
					</Link>
				</div>
			)
		}

		//Updating print data
		setdataPrintState(dataPrint)
	}

	const onChangeDate = (date, dateString) => {
		//Update date from date picker for case 4
		// console.log('datestring')
		setdateFieldValue(date)
		if (dateString !== null && date !== null) {
			// console.log('cas date 1')
			//Cuurent array being filtered
			let currentArray = []
			//holding the selected data
			let data = []
			//Printing values to List Item
			let dataPrint = []
			switch (priorityChoice) {
				case 0:
					// default all tasks
					currentArray = dataArrayState.slice()
					break
				case 1:
					// Normal tasks
					currentArray = normal.slice()
					break
				case 2:
					// Imprtant tasks
					currentArray = important.slice()
					break
				case 3:
					// Urgent tasks
					currentArray = urgent.slice()
					break
				case 4:
					// Completed tasks
					currentArray = completed.slice()
					break
					default:
			}

			for (let i = 0; i < currentArray.length; i++) {
				if (currentArray[i][1] === dateString) {
					data.push(currentArray[i])
				}
			}

			//Reconstruire dataPrintState
			for (let i = 0; i < data.length; i++) {
				//Pour chaque data dans l'element courant

				dataPrint.push(
					<div
						key={data[i][0] + data[i][1]}
						className='listtask-item'
					>
						<Link to='/AddTasks'>
							<div onClick={(e) => handleChangeClick(e)}>
								<span> {data[i][1]} </span>
								<span> {data[i][2]} </span>
								<span> {data[i][3]} </span>
								<span> {data[i][4]} </span>
							</div>
						</Link>
					</div>
				)
			}

			setdataPrintState(dataPrint)
			data = []
			currentArray = []
			dataPrint = []
		}
	}

	// function onShowSizeChange(current, pageSize) {
	// 	console.log(current, pageSize)
	// }

	// function onChangePage(page, pageSize) {
	// 	console.log(page, pageSize)
	// }

	function handleSortDate(value) {
		// console.log(`selected ${value}`)
		//Set filter value for the handleSearch
		setFilterAscDesc(value)
		//Cuurent array being filtered
		let currentArray = []
		//holding the selected data
		let data = []
		//Printing values to List Item
		let dataPrint = []
		switch (priorityChoice) {
			case 0:
				// default all tasks
				currentArray = dataArrayState.slice()
				break
			case 1:
				// Normal tasks
				currentArray = normal.slice()
				break
			case 2:
				// Imprtant tasks
				currentArray = important.slice()
				break
			case 3:
				// Urgent tasks
				currentArray = urgent.slice()
				break
			case 4:
				// Completed tasks
				currentArray = completed.slice()
				break
				default:
		}

		if (value === 'ASC') {
			data = currentArray.sort((a, b) => new Date(b[1]) - new Date(a[1]))
		} else if (value === 'DESC') {
			data = currentArray.sort((a, b) => new Date(a[1]) - new Date(b[1]))
		}
		//Reconstruire dataPrintState
		for (let i = 0; i < data.length; i++) {
			//Pour chaque data dans l'element courant

			dataPrint.push(
				<div key={data[i][0] + data[i][1]} className='listtask-item'>
					<Link to='/AddTasks'>
						<div onClick={(e) => handleChangeClick(e)}>
							<span> {data[i][1]} </span>
							<span> {data[i][2]} </span>
							<span> {data[i][3]} </span>
							<span> {data[i][4]} </span>
						</div>
					</Link>
				</div>
			)
		}

		setdataPrintState(dataPrint)
		data = []
		currentArray = []
		dataPrint = []
	}

	return (
		<div className='listtask-main'>
		<div className='listtask-main2'>
			<h1 className='listtask-main2-title'>Search for tasks</h1>
			<Radio.Group
				onChange={handleChangePriority}
				value={priorityChoice}
				className='listtask-radio'
			>
				<Radio.Button
					value={1}
					className='ant-radio-button-wrapper hello'
				>
					Normal
				</Radio.Button>
				<Radio.Button value={2}>Important</Radio.Button>
				<Radio.Button value={3}>Urgent</Radio.Button>
				<Radio.Button value={4}>Completed</Radio.Button>
				<Radio.Button value={0}>All</Radio.Button>
			</Radio.Group>

			<DatePicker
				ref={inputEl}
				onChange={onChangeDate}
				value={dateFieldValue}
				className='listtask-date'
			/>

			<br></br>
			<Select
				className='listtask-select'
				placeholder='Oder By Date'
				style={{ width: 166 }}
				onChange={handleSortDate}
			>
				<Option value='ASC'>Ascending Dates</Option>
				<Option value='DESC'>Descending Dates</Option>
			</Select>

			<Search
				className='listtask-search'
				ref={inputSearch}
				placeholder='input search text'
				onSearch={handleSearch}
				onChange={handleChangeSearch}
				enterButton
			/>
			</div>

			<Divider className='listtask-divider'>
				Your List
			</Divider>

			<List
				className='listtask-list'
				size='large'
				header={
					<div>
						<i className='fas fa-calendar-week'></i> <span> Start</span>
					</div>
				}
				footer={
					<div className='fasfa-stopwatch'>
						<i className='fas fa-stopwatch'></i> <span> End</span>
					</div>
				}
				bordered
				dataSource={dataPrintState}
				renderItem={(item) => <List.Item>{item}</List.Item>}
				pagination
			/>
		</div>
	)
}
