import React, { useState } from 'react'
import CalendarComp from './components/CalendarComp'
import AddEditTasks from './components/AddEditTasks'
import ListAllTasks from './components/ListAllTasks'
import TimelineComp from './components/TimelineComp'
import moment from 'moment'
import SavingChoice from './components/SavingChoice'
import 'antd/dist/antd.css'
import {
	BrowserRouter as Router,
	Switch,
	Route,
	Link,
	Redirect,
} from 'react-router-dom'
import './App.css'
import './components/css/style.min.css'

import { Layout, Menu} from 'antd'

const { Header, Footer } = Layout

export default function App() {

	let savingMode = [
		'',
		' Not Active',
		' Local',
		' Remote',
	]

	const [savingAccess, setsavingAccess] = useState(0)

	const [state, setState] = useState({
		value: moment(Date.Now),
		selectedValue: moment(Date.Now),
	})

	const [data, setdata] = useState({})

	const [inputArray, setinputArray] = useState(new Array(24).fill(null))
	const [priorityArray, setpriorityArray] = useState(new Array(24).fill(null))

	function getCurrentData(data, setData, setState, value, selectedValue) {
		let currentData = {}
		if (data !== null && data !== undefined) {
			if (Object.entries(data).length <= 0) {
				let localdata1 = localStorage.getItem('data')

				let localdata2 = JSON.parse(localdata1)
				if (localdata2 !== null || localdata2 !== undefined) {
					setData(localdata2)
					currentData = localdata2
					setState((prev) => ({
						...prev,
						value: value,
						selectedValue: selectedValue,
					}))
				} else {
					currentData = {}
				}
			} else {
				currentData = data
			}
			return currentData
		}
	}

	React.useEffect(() => {
		// console.log('//// IN useEFFECT //////')

		// console.log('state')
		// console.log(state)

		console.log('data')
		console.log(data)
		if (data !== null && data !== undefined) {
			if (Object.entries(data).length > 0) {
				console.log('saving to localstorage')
				let localdata = JSON.stringify(data)
				localStorage.setItem('data', localdata)
			} else {
				let localdata1 = localStorage.getItem('data')
				// console.log(localdata1)

				let localdata2 = JSON.parse(localdata1)
				if (localdata2 !== null || localdata2 !== undefined) {
					setdata(localdata2)
					setState((prev) => ({
						...prev,
						value: state.value,
						selectedValue: state.selectedValue,
					}))
				}
			}
		}

		// console.log('data after')
		// console.log(data)

		// console.log('inputArray')
		// console.log(inputArray)
		// console.log('priorityArray')
		// console.log(priorityArray)
	}, [state, data])

	const savingOption = (key) => {
		setsavingAccess(key)
		document.getElementById('connect').className =
			'ant-menu-item ant-menu-item-only-child'
		// if (key === 2) {
		// 	//Db connection
		// 	console.log('key === 2')
		// } else if (key === 1) {
			
		// 	console.log('key === 1')
		// }
	}

	React.useEffect(() => {
		document.getElementById('connect').className =
			'ant-menu-item ant-menu-item-only-child'
	}, [])
	// const [value, setValue] = useState(moment('2017-01-25'))
	// const [selectedValue, setSelectedValue] = useState(moment('2017-01-25'))

	return (
		<Router>
			<Layout className='layout'>
				<Header>
					<div className='logo' />
					<Menu
						theme='dark'
						mode='horizontal'
						defaultSelectedKeys={['2']}
					>
						<Menu.Item key='1' id='calendo'>
							<Link to='/' id='test2'>
								<i className='far fa-calendar-alt'> </i> Calendo
							</Link>
						</Menu.Item>
						<Menu.Item key='2' id='addTask'>
							<Link to='/AddTasks'>
								<i className='fas fa-tasks'> </i> Add Tasks
							</Link>
						</Menu.Item>
						<Menu.Item key='3' id='listTask'>
							<Link to='/ListTasks'>
								<i className='fas fa-stream'> </i> Show Tasks
							</Link>
						</Menu.Item>
						<Menu.Item key='4' id='timeline'>
							<Link to='/Timeline'>
								<i className='fas fa-road'> </i> Timeline
							</Link>
						</Menu.Item>
						<Menu.Item key='5' id='savingMode'>
							<Link
								to='/connect'
								id='connect'
								onClick={() => setsavingAccess(0)}
							>
								<i className='fas fa-save'></i> Saving mode:{' '}
								{savingMode[savingAccess]}
							</Link>
						</Menu.Item>
					</Menu>
				</Header>
				{/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
				<Switch>
					<Route exact path='/'>
						{!savingAccess ? (
							<Redirect to='/connect' />
						) : (
							<CalendarComp
								state={state}
								setState={setState}
								data={data}
								setdata={setdata}
								inputArray={inputArray}
								setinputArray={setinputArray}
								priorityArray={priorityArray}
								setpriorityArray={setpriorityArray}
								getCurrentData={getCurrentData}
							/>
						)}
					</Route>
					<Route path='/AddTasks'>
						{!savingAccess ? (
							<Redirect to='/connect' />
						) : (
							<AddEditTasks
								state={state}
								setState={setState}
								data={data}
								setdata={setdata}
								inputArray={inputArray}
								setinputArray={setinputArray}
								priorityArray={priorityArray}
								setpriorityArray={setpriorityArray}
								getCurrentData={getCurrentData}
							/>
						)}
					</Route>
					<Route path='/ListTasks'>
						{!savingAccess ? (
							<Redirect to='/connect' />
						) : (
							<ListAllTasks
								state={state}
								setState={setState}
								data={data}
								setdata={setdata}
								inputArray={inputArray}
								setinputArray={setinputArray}
								priorityArray={priorityArray}
								setpriorityArray={setpriorityArray}
								getCurrentData={getCurrentData}
							/>
						)}
					</Route>
					<Route path='/Timeline'>
						{!savingAccess ? (
							<Redirect to='/connect' />
						) : (
							<TimelineComp
								state={state}
								setState={setState}
								data={data}
								setdata={setdata}
								getCurrentData={getCurrentData}
							/>
						)}
					</Route>
					<Route exact path='/connect'>
						{savingAccess ? (
							<Redirect to='/' />
						) : (
							<SavingChoice
								state={state}
								setState={setState}
								data={data}
								setdata={setdata}
								inputArray={inputArray}
								setinputArray={setinputArray}
								priorityArray={priorityArray}
								setpriorityArray={setpriorityArray}
								setsavingAccess={setsavingAccess}
								savingOption={savingOption}
							/>
						)}
					</Route>
				</Switch>
				<Footer className='footer' style={{ textAlign: 'center' }}>
					Ant Design ©2020 Created with Ant UED
				</Footer>
			</Layout>
		</Router>
	)
}
