import React, { Component } from 'react'
import { Input } from 'antd'
import { Radio } from 'antd'
import { Layout } from 'antd'
import moment from 'moment'
import CalendarComp2 from './CalendarComp2'

const { Content } = Layout

export default class AddEditTasks extends Component {
	constructor(props) {
		super(props)

		this.state = {
			year: this.props.state.selectedValue.format('YYYY'),
			month: this.props.state.selectedValue.format('MM'),
			day: this.props.state.selectedValue.format('DD'),
			fullDate: moment(
				this.props.state.selectedValue.format('YYYY') +
					this.props.state.selectedValue.format('MM') +
					this.props.state.selectedValue.format('DD')
			),
			value: this.props.state.value,
			arrayValue: this.props.inputArray,
			priorityValue: this.props.priorityArray,
			keyUpdate: 0,
		}
		this.updateArrayValue = this.updateArrayValue.bind(this)
		this.handleChange = this.handleChange.bind(this)
		// this.setState = this.setState.bind(this)
	}

	recycleArrays = () => {
		this.props.setinputArray(new Array(24).fill(null))
		this.props.setpriorityArray(new Array(24).fill(null))

		//Mettre également input Array à jour
		this.setState({
			arrayValue: new Array(24).fill(null),
			priorityValue: new Array(24).fill(null),
		})
		// console.log('////Current Object /////')
		// console.log('New EMPTY ArrayValue')
		// console.log(this.state.arrayValue)
		// console.log('New EMPTY PriorityValue')
		// console.log(this.state.priorityValue)

		// console.log('////Top Object /////')
		// console.log('New EMPTY ArrayPriority')
		// console.log(this.props.priorityArray)
		// console.log('New EMPTY InputArray')
		// console.log(this.props.inputArray)
	}

	updateKey = (year, month, day) => {
		//Update those states from embed calendar
		this.setState(
			{
				keyUpdate: this.state.keyUpdate + 1,
				arrayValue: this.props.inputArray,
				priorityValue: this.props.priorityArray,
				year: year,
				month: month,
				day: day,
				fullDate: moment(year + month + day),
			},
			() => {
				// console.log('Values updated')

				// console.log(this.state.fullDate.format('YYYY-MM-DD'))
				// console.log(this.props.state.selectedValue.format('YYYY-MM-DD'))
				// console.log('day')
				// console.log(this.state.day)
				let val = moment(year + month + day)
				// console.log(val)

				if (Object.entries(this.props.data).length > 0) {
					// console.log('1')
					if (this.props.data[val.format('YYYY')]) {
						// console.log('2')
						if (
							this.props.data[val.format('YYYY')][
								val.format('MM')
							]
						) {
							// console.log('3')
							if (
								this.props.data[val.format('YYYY')][
									val.format('MM')
								][val.format('DD')]
							) {
								// console.log('4')
								let tempArrayValue = new Array(24).fill(null)
								let tempArrayPriority = new Array(24).fill(null)
								//Because there can be no entries for that hour
								if (
									Object.entries(
										this.props.data[val.format('YYYY')][
											val.format('MM')
										][val.format('DD')].content
									).length > 0
								) {
									Object.keys(
										this.props.data[val.format('YYYY')][
											val.format('MM')
										][val.format('DD')].content
									).map((key) => {
										// console.log(this.props.data[this.state.year][this.state.month][this.state.day].content[key])
										tempArrayValue[key] = this.props.data[
											val.format('YYYY')
										][val.format('MM')][
											val.format('DD')
										].content[key].valueInput
										tempArrayPriority[
											key
										] = this.props.data[val.format('YYYY')][
											val.format('MM')
										][val.format('DD')].content[
											key
										].priority
									})

									// console.log(
									// 	'////////////A TEST //////////////////////////'
									// )

									// console.log(tempArrayValue)
									// console.log(tempArrayPriority)
									this.props.setinputArray(tempArrayValue)
									this.props.setpriorityArray(
										tempArrayPriority
									)

									//Mettre également input Array à jour
									this.setState({
										arrayValue: tempArrayValue,
										priorityValue: tempArrayPriority,
									})
									// console.log('////Current Object /////')

									// console.log('New ArrayValue')
									// console.log(this.state.arrayValue)
									// console.log('New PriorityValue')
									// console.log(this.state.priorityValue)

									// console.log('////Top Object /////')
									// console.log('New ArrayPriority')
									// console.log(this.props.priorityArray)
									// console.log('New InputArray')
									// console.log(this.props.inputArray)
								} else {
									// console.log(
									// 	'////////////A TEST EMPTY CAS 0//////////////////////////'
									// )
									this.recycleArrays()
								}
							} else {
								// console.log(
								// 	'////////////A TEST EMPTY CAS 1//////////////////////////'
								// )
								this.recycleArrays()
							}
						} else {
							// console.log(
							// 	'////////////A TEST EMPTY CAS 2//////////////////////////'
							// )

							this.recycleArrays()
						}
					} else {
						// console.log(
						// 	'////////////A TEST EMPTY CAS 3//////////////////////////'
						// )
						this.recycleArrays()
					}
				} else {
					// console.log(
					// 	'////////////A TEST EMPTY CAS 4//////////////////////////'
					// )
					this.recycleArrays()
				}
			}
		)
	}

	componentDidMount() {
		// console.log('Mounting addEditTask')
		//On page refresh, reset the data
		let currentData = this.props.getCurrentData(this.props.data, this.props.setdata, this.props.setState, this.props.state.value, this.props.state.selectedValue)

		//Setting the links
		document.getElementById('addTask').className = 'ant-menu-item ant-menu-item-only-child ant-menu-item-selected'
		document.getElementById('calendo').className = 'ant-menu-item ant-menu-item-only-child'
		document.getElementById('listTask').className = 'ant-menu-item ant-menu-item-only-child'
		document.getElementById('timeline').className = 'ant-menu-item ant-menu-item-only-child'
		
		if(currentData !== null && currentData !== undefined && this.props.data !== null && this.props.data !== undefined){
			
			
		let val = this.props.state.value
		
		if (Object.entries(currentData).length > 0) {
			// console.log('1')
			if (currentData[val.format('YYYY')]) {
				// console.log('2')
				if (currentData[val.format('YYYY')][val.format('MM')]) {
					// console.log('3')
					if (
						currentData[val.format('YYYY')][val.format('MM')][
							val.format('DD')
						]
					) {
						// console.log('4')
						let tempArrayValue = new Array(24).fill(null)
						let tempArrayPriority = new Array(24).fill(null)
						//Because there can be no entries for that hour
						if (
							Object.entries(
								currentData[val.format('YYYY')][val.format('MM')][
									val.format('DD')
								].content
							).length > 0
						) {
							Object.keys(
								currentData[val.format('YYYY')][val.format('MM')][
									val.format('DD')
								].content
							).map((key) => {
								// console.log(this.props.data[this.state.year][this.state.month][this.state.day].content[key])
								tempArrayValue[key] =
									currentData[val.format('YYYY')][val.format('MM')][
										val.format('DD')
									].content[key].valueInput
								tempArrayPriority[key] =
									currentData[val.format('YYYY')][val.format('MM')][
										val.format('DD')
									].content[key].priority
							})

							// console.log(tempArrayValue)
							// console.log(tempArrayPriority)
							this.props.setinputArray(tempArrayValue)
							this.props.setpriorityArray(tempArrayPriority)
							//Mettre également input Array à jour
							this.setState({
								arrayValue: tempArrayValue,
								priorityValue: tempArrayPriority,
							})
						}
					}
				}
			}
		}
	}
	}
	componentWillUnmount() {
		// console.log('Unmounting AddEditTask')

		//2 arrays in app.js back to null
		this.props.setinputArray(new Array(24).fill(null))
		this.props.setpriorityArray(new Array(24).fill(null))

		//2 array from here back to null
		//Mettre également input Array à jour
		this.setState({
			arrayValue: new Array(24).fill(null),
			priorityValue: new Array(24).fill(null),
		})
	}

	componentDidUpdate(prevProps, prevState) {
		// console.log(this.state.data)
		// console.log(':)')
		// console.log(this.props.state)
		// console.log(this.inputRef.current)
		// console.log('Comp Update Array value')
		// console.log(this.state.arrayValue)
		// console.log('Comp Update input Array')
		// console.log(this.props.inputArray)
		// console.log('Comp PriorityValue')
		// console.log(this.state.priorityValue)
		// console.log('Comp ArrayPriority')
		// console.log(this.props.priorityArray)
	}

	updateArrayValue(e, index) {
		const arr = this.state.arrayValue.slice()
		arr[index] = e.target.value
		this.setState({
			arrayValue: arr,
		})
		// console.log(this.state.arrayValue[index])
	}

	saveChange(param, index, valuePriority, valueInput) {
		//Priority
		let completed = false
		if (valuePriority === 'completed') {
			completed = true
		}
		//Update array of priorities
		let arrayPriority = this.state.priorityValue.slice()
		arrayPriority[index] = param.target.value
		this.setState({
			priorityValue: arrayPriority,
		})
		// console.log('Entering save')

		// console.log(this.state.fullDate)
		// console.log(this.state.day)

		//Create data if doesn't exist and update
		if (this.props.data !== null && this.props.data !== undefined && Object.entries(this.props.data).length > 0) {
			if (this.props.data[this.state.year]) {
				if (this.props.data[this.state.year][this.state.month]) {
					//Si le mois existe pas
					if (
						this.props.data[this.state.year][this.state.month][
							this.state.day
						]
					) {
						//Si le content existe
						this.props.setdata((prevState, props) => ({
							[this.state.year]: {
								...prevState[this.state.year],
								[this.state.month]: {
									...prevState[this.state.year][
										this.state.month
									],
									[this.state.day]: {
										...prevState[this.state.year][
											this.state.month
										][this.state.day],
										fullDate: this.state.fullDate,
										content: {
											...prevState[this.state.year][
												this.state.month
											][this.state.day].content,
											[index]: {
												valueInput: valueInput,
												priority: valuePriority,
												completed: completed,
											},
										},
									},
								},
							},
						}))
					} else {
						//Si le content n'existe pas
						let content = {
							fullDate: this.state.fullDate,
							content: {
								[index]: {
									valueInput: valueInput,
									priority: valuePriority,
									completed: completed,
								},
							},
						}

						this.props.setdata((prevState, props) => ({
							[this.state.year]: {
								...prevState[this.state.year],
								[this.state.month]: {
									...prevState[this.state.year][
										this.state.month
									],
									[this.state.day]: content,
								},
							},
						}))
					}
				} else {
					//Si le mois n'existe pas
					let monthData = {
						[this.state.day]: {
							fullDate: this.state.fullDate,
							content: {
								[index]: {
									valueInput: valueInput,
									priority: valuePriority,
									completed: completed,
								},
							},
						},
					}

					this.props.setdata((prevState, props) => ({
						[this.state.year]: {
							...prevState[this.state.year],
							[this.state.month]: monthData,
						},
					}))
				}
			} else {
				//Si le mois n'existe pas
				let yearData = {
					[this.state.month]: {
						[this.state.day]: {
							fullDate: this.state.fullDate,
							content: {
								[index]: {
									valueInput: valueInput,
									priority: valuePriority,
									completed: completed,
								},
							},
						},
					},
				}

				this.props.setdata((prevState, props) => ({
					...prevState,
					[this.state.year]: yearData,
				}))
			}
		} else {
			//Build the object 1 that will be merged with state.data
			// console.log('////////////NEW////////////////')

			let data = {
				[this.state.year]: {
					[this.state.month]: {
						[this.state.day]: {
							fullDate: this.state.fullDate,
							content: {
								[index]: {
									valueInput: valueInput,
									priority: valuePriority,
									completed: completed,
								},
							},
						},
					},
				},
			}

			// this.setState({
			// 	dataExist: true,
			// })

			this.props.setdata(data)
		}
		//Just calling setstate to trigger useEffect
		this.props.setState((prev) => ({
			...prev,
		}))
	}

	removeByKey(myObj, deleteKey) {
		return Object.keys(myObj)
			.filter((key) => key !== deleteKey)
			.reduce((result, current) => {
				result[current] = myObj[current]
				return result
			}, {})
	}

	deleteData(param, index, valuePriority, valueInput) {
		let removeYear = false
		let removeMonth = false
		let removeDay = false
		let removeContent = false

		if (Object.entries(this.props.data).length > 0) {
			//Combien d année reste
			if (Object.entries(this.props.data).length === 1) {
				//1
				removeYear = true
			}

			//Combien de mois reste
			if (Object.entries(this.props.data[this.state.year]).length === 1) {
				//1
				removeMonth = true
			}
			//Combien de jour reste
			if (
				Object.entries(
					this.props.data[this.state.year][this.state.month]
				).length === 1
			) {
				//2
				removeDay = true
			}
			//Combien il en reste dans le jour
			if (
				Object.entries(
					this.props.data[this.state.year][this.state.month][
						this.state.day
					].content
				).length === 1
			) {
				//2
				removeContent = true
			}

			if (removeYear && removeMonth && removeDay && removeContent) {
				this.props.setdata({})
				this.updateKey(
					this.state.year,
					this.state.month,
					this.state.day
				)
			}

			if (!removeYear && removeMonth && removeDay && removeContent) {
				let data = this.props.data
				delete data[this.state.year]
				this.props.setdata(data)
				this.updateKey(
					this.state.year,
					this.state.month,
					this.state.day
				)
			}

			if (!removeMonth && removeDay && removeContent) {
				// console.log('cas 3')

				let data = this.props.data
				delete data[this.state.year][this.state.month]
				this.props.setdata(data)
				this.updateKey(
					this.state.year,
					this.state.month,
					this.state.day
				)
			}

			if (!removeDay && removeContent) {
				// console.log('cas 4')

				let data = this.props.data
				delete data[this.state.year][this.state.month][this.state.day]
				this.props.setdata(data)
				this.updateKey(
					this.state.year,
					this.state.month,
					this.state.day
				)
			}

			if (!removeContent) {
				// console.log('cas 5')

				let data = this.props.data
				delete data[this.state.year][this.state.month][this.state.day]
					.content[index]
				this.props.setdata(data)
				this.updateKey(
					this.state.year,
					this.state.month,
					this.state.day
				)
			}

			this.props.setState((prev) => ({
				...prev,
			}))
		}
	}

	handleChange(param) {
		//Get the id from the priority fields !
		let index = param.target.priority
		let valuePriority = param.target.value

		
		

		//Value
		let valueInput = null
		if (this.state.arrayValue !== null) {
			valueInput = this.state.arrayValue[index]
		}

		if (valueInput.length > 60) {
			valueInput = valueInput.slice(0, 60)
		}
		// console.log('/////////////////////////////////')
		
		// console.log(valueInput)

		if (valueInput === null) {
			let arrayPriority = this.state.priorityValue.slice()
			param.target.value = null
			arrayPriority[index] = param.target.value
		} else if (valuePriority === 'delete') {
			// console.log('DELETE')
			this.deleteData(param, index, valuePriority, valueInput)
		} else if (valueInput === '') {
			param.target.value = valuePriority
		} else {
			this.saveChange(param, index, valuePriority, valueInput)
		}
	}

	render() {
		return (
			<Content style={{ padding: '0 50px' }} className='edit-content'>
				<div className='calendar-edit'>
					<CalendarComp2
						state={this.props.state}
						setState={this.props.setState}
						data={this.props.data}
						setdata={this.props.setdata}
						inputArray={this.props.inputArray}
						setinputArray={this.props.setinputArray}
						priorityArray={this.props.priorityArray}
						setpriorityArray={this.props.setpriorityArray}
						update={this.state.keyUpdate}
						setupdate={this.updateKey}
						arrayValue={this.state.arrayValue}
					/>
				</div>
				<div className='block'>
					{this.state.arrayValue.map((tasks, index) => (
						<div
							key={index + 'div' + this.state.keyUpdate}
							className='block-child'
						>
							<label className='block-label'>
								{index + 'h00'}
							</label>
							<Input
								className='block-input'
								key={
									index + 'superinput' + this.state.keyUpdate
								}
								id={'input' + index}
								name={index}
								onChange={(e) =>
									this.updateArrayValue(e, index)
								}
								value={
									this.state.arrayValue[index]
										? this.state.arrayValue[index]
										: ''
								}
							/>

							<Radio.Group
								className='radio1'
								key={
									index + 'superradio' + this.state.keyUpdate
								}
								onChange={this.handleChange}
								defaultValue={
									this.state.priorityValue[index]
										? this.state.priorityValue[index]
										: ''
								}
								value={
									this.state.priorityValue[index]
										? this.state.priorityValue[index]
										: ''
								}
							>
								<Radio.Button
									className={'button-none'}
									name='normal'
									value='normal'
									priority={index}
								>
									Normal
								</Radio.Button>

								<Radio.Button
									className={'button1'}
									name='normal'
									value='normal'
									priority={index}
								>
									Normal
								</Radio.Button>

								<Radio.Button
									className={'button2'}
									name='important'
									value='important'
									priority={index}
								>
									Important
								</Radio.Button>

								<Radio.Button
									className={'button3'}
									name='urgent'
									value='urgent'
									priority={index}
								>
									Urgent
								</Radio.Button>

								<Radio.Button
									className={'button4'}
									name='completed'
									value='completed'
									priority={index}
								>
									Completed
								</Radio.Button>

								<Radio.Button
									className={'button5'}
									name='delete'
									value='delete'
									priority={index}
								>
									Delete
								</Radio.Button>
							</Radio.Group>
						</div>
					))}
				</div>
			</Content>
		)
	}
}
