import React, { useEffect } from 'react'
import { Input, Space } from 'antd'
import { EyeInvisibleOutlined, EyeTwoTone } from '@ant-design/icons'
import { Button, Layout } from 'antd'

const { Content } = Layout

function SavingChoice({ setsavingAccess, savingOption }) {

	useEffect(() => {
		document.getElementById('savingMode').className =
			'ant-menu-item ant-menu-item-only-child'
		document.getElementById('calendo').className =
			'ant-menu-item ant-menu-item-only-child'
		document.getElementById('addTask').className =
			'ant-menu-item ant-menu-item-only-child'
		document.getElementById('listTask').className =
			'ant-menu-item ant-menu-item-only-child'
		document.getElementById('timeline').className =
            'ant-menu-item ant-menu-item-only-child'
	}, [])

	return (
		<Content style={{ padding: '0 50px' }} className='edit-content'>
			<div>
				<div className='login'>
					<h1 className='login-title'>
						<i id='logo' className='far fa-calendar'></i> Welcome to
						Calendo, your secret agenda
					</h1>
				</div>
				<div className='saving'>
					<div className='saving-choice1'>
						<h1>Data will be stored in the local storage only</h1>
						<Button
							onClick={() => savingOption(1)}
							size='large'
							type='primary'
						>
							Don't save the data
						</Button>
					</div>

                    <div className='saving-choice2'>
                    <img src={require('./assets/demo.png')} alt="DEMO" className='demo' />
						<h1>
							Data will be saved in your local storage and local
							MongoDB
						</h1>
						<Button
							onClick={() => savingOption(2)}
							size='large'
							type='primary'
						>
							Save the data to local MongoDB
						</Button>
					</div>
                    <div className='saving-choice3'>
                    <img src={require('./assets/demo.png')} alt="DEMO" className='demo' />
						<h1>
							Data will be saved in your local storage and remote
							MongoDB
						</h1>
						<Space direction='vertical'>
							<label>MongoDB username</label>
							<Input placeholder='MongoDB username' />
							<label>MongoDB password</label>
							<Input.Password
								placeholder='MongoDb password'
								iconRender={(visible) =>
									visible ? (
										<EyeTwoTone />
									) : (
										<EyeInvisibleOutlined />
									)
								}
							/>
							<Button
								onClick={() => savingOption(3)}
								size='large'
								type='primary'
							>
								Save the data to remote MongoDB
							</Button>
						</Space>
					</div>
				</div>
			</div>
		</Content>
	)
}

export default SavingChoice
